
  import { computed, defineComponent, PropType, ref } from 'vue';
  import { maska } from 'maska';
  import Icon from '@/components/icon/Icon.vue';
  import { Icon as IconProps } from '@/components/icon/Icon';
  import { InputState, InputSize, InputMask } from '@/components/input/Input';

  export default defineComponent({
    name: 'Input',
    components: { Icon },
    directives: { maska },
    props: {
      modelValue: {
        type: [String, Number],
      },
      placeholder: {
        type: String,
        default: '',
      },
      label: {
        type: String,
        default: '',
      },
      caption: {
        type: String,
        default: '',
      },
      state: {
        type: String as PropType<InputState>,
        default: 'default',
        validator: (state: InputState) => {
          const states: InputState[] = ['default', 'error'];
          return states.includes(state);
        },
      },
      id: {
        type: String,
      },
      disabled: {
        type: Boolean,
        default: false,
      },
      size: {
        type: String as PropType<InputSize>,
        default: 'm',
        validator: (value: InputSize) => ['s', 'm', 'l'].includes(value),
      },
      icon: {
        type: Object as PropType<IconProps | null>,
        default: null,
      },
      badge: {
        type: String,
        default: '',
      },
      mask: {
        type: Object as PropType<InputMask | null>,
        default: null,
      },
    },
    emits: ['update:modelValue', 'input', 'paste', 'focus', 'blur', 'mask'],
    setup(props, { emit }) {
      const input = ref<HTMLInputElement | null>(null);
      const isFocused = ref(false);

      const message = computed({
        get: () => props.modelValue,
        set: (value) => {
          emit('update:modelValue', value);
        },
      });

      const boxClass = computed(() => {
        return {
          focus: isFocused.value,
          [`${props.state}`]: true,
          [`${props.size}`]: true,
          disabled: props.disabled,
        };
      });

      const labelClass = computed(() => {
        return {
          [`${props.size}`]: true,
        };
      });

      const inputClass = computed(() => {
        return {
          [`${props.size}`]: true,
          [`${props.state}`]: true,
        };
      });

      const captionClass = computed(() => {
        return {
          [`${props.state}`]: true,
        };
      });

      const badgeClass = computed(() => {
        return {
          focus: isFocused.value,
          [`${props.size}`]: true,
          disabled: props.disabled,
        };
      });

      const focus = () => {
        input.value?.focus();
      };

      const handleInput = (event: InputEvent) => {
        emit('input', (event.target as HTMLInputElement).value);
      };

      const handlePaste = (event: Event) => {
        emit('paste', event);
      };

      const handleFocus = () => {
        isFocused.value = true;
        emit('focus');
      };

      const handleBlur = () => {
        isFocused.value = false;
        emit('blur');
      };

      const handleIconClick = () => {
        console.log('icon click');
      };

      const handleMask = (event: Event) => {
        emit('mask', (event.target as HTMLInputElement).value);
      };

      return {
        message,
        boxClass,
        labelClass,
        inputClass,
        captionClass,
        badgeClass,
        input,
        isFocused,
        focus,
        handleInput,
        handlePaste,
        handleIconClick,
        handleFocus,
        handleBlur,
        handleMask,
      };
    },
  });
