
  import { computed, defineComponent, PropType } from 'vue';
  import { CheckboxSize } from '@/components/checkbox/Checkbox';
  import ic12check from '@/assets/icons/ic12-check.svg';
  import ic16check from '@/assets/icons/ic16-check.svg';

  export default defineComponent({
    name: 'Checkbox',
    inheritAttrs: false,
    emits: ['update:modelValue'],
    props: {
      modelValue: {
        type: Boolean,
        default: false,
      },
      disabled: {
        type: Boolean,
        default: false,
      },
      indeterminate: {
        type: Boolean,
        default: false,
      },
      id: {
        type: String,
      },
      label: {
        type: String,
      },
      caption: {
        type: String,
      },
      error: {
        type: String,
      },
      size: {
        type: String as PropType<CheckboxSize>,
        default: 'm',
        validator: (value: string) => ['m', 'l'].includes(value),
      },
    },
    setup(props, { emit }) {
      const boxClass = computed(() => {
        return {
          [`${props.size}`]: true,
          checked: props.modelValue,
          error: props.error,
          indeterminate: props.indeterminate,
          disabled: props.disabled,
        };
      });

      const labelClass = computed(() => {
        return {
          checked: props.modelValue,
          indeterminate: props.indeterminate,
          disabled: props.disabled,
          'caption-16px-medium': props.size === 'l',
          'text-12px-regular': props.size === 'm',
        };
      });

      const captionClass = computed(() => {
        return {
          [`${props.size}`]: true,
          error: props.error,
          'text-12px-regular': props.size === 'l' || props.size === 'm',
        };
      });

      const imgSrc = computed(() => {
        return props.size === 'l' ? ic16check : ic12check;
      });

      const onClick = () => {
        emit('update:modelValue', !props.modelValue);
      };

      return {
        imgSrc,
        boxClass,
        captionClass,
        labelClass,
        onClick,
      };
    },
  });
