
  import { defineComponent, PropType, computed } from 'vue';
  import Icon from '@/components/icon/Icon.vue';
  import { ButtonSize, ButtonMode } from './Button';
  import { Icon as IconProps } from '@/components/icon/Icon';

  export default defineComponent({
    name: 'Button',
    components: { Icon },
    props: {
      size: {
        type: String as PropType<ButtonSize>,
        default: 's',
        validator: (value: ButtonSize) => {
          const sizes: ButtonSize[] = ['s', 'm', 'l'];
          return sizes.includes(value);
        },
      },
      mode: {
        type: String as PropType<ButtonMode>,
        default: 'primary',
        validator: (type: ButtonMode) => {
          const types: ButtonMode[] = ['primary', 'secondary', 'info', 'state'];
          return types.includes(type);
        },
      },
      icon: {
        type: Object as PropType<IconProps | null>,
        default: null,
      },
      loading: {
        type: Boolean,
        default: false,
      },
      iconOnly: {
        type: Boolean,
        default: false,
      },
    },
    setup(props) {
      const buttonClasses = computed(() => ({
        [`${props.size}`]: true,
        'with-icon': props.icon,
        [`${props.mode}`]: props.mode !== 'primary',
        loading: props.loading,
        'icon-only': props.iconOnly,
      }));

      return { buttonClasses };
    },
  });
