
  import { computed, defineComponent, PropType, ref, watchEffect } from 'vue';
  import { TextareaState } from '@/components/textarea/Textarea';

  export default defineComponent({
    name: 'Textarea',
    props: {
      modelValue: {
        type: [String, Number],
      },
      placeholder: {
        type: String,
        default: '',
      },
      label: {
        type: String,
        default: '',
      },
      caption: {
        type: String,
        default: '',
      },
      state: {
        type: String as PropType<TextareaState>,
        default: 'default',
        validator: (state: TextareaState) => {
          const states: TextareaState[] = ['default', 'error'];
          return states.includes(state);
        },
      },
      id: {
        type: String,
      },
      disabled: {
        type: Boolean,
        default: false,
      },
    },
    emits: ['update:modelValue', 'input', 'paste'],
    setup(props, { emit }) {
      const textarea = ref<HTMLTextAreaElement | null>(null);
      const isFocused = ref(false);

      const message = computed({
        get: () => props.modelValue,
        set: (value) => {
          emit('update:modelValue', value);
        },
      });

      const boxClass = computed(() => {
        return {
          focus: isFocused.value,
          [`${props.state}`]: true,
          disabled: props.disabled,
        };
      });

      const textareaClass = computed(() => {
        return {
          [`${props.state}`]: true,
        };
      });

      const captionClass = computed(() => {
        return {
          [`${props.state}`]: true,
        };
      });

      watchEffect(() => {
        if (!textarea.value?.scrollHeight) return;
        adjustHeight(textarea.value);
      });

      const focus = () => {
        textarea.value?.focus();
      };

      const handleInput = (event: InputEvent) => {
        adjustHeight(event.target as HTMLTextAreaElement);
        emit('input', (event.target as HTMLTextAreaElement).value);
      };

      const handlePaste = (event: Event) => {
        emit('paste', event);
      };

      const adjustHeight = (textarea: HTMLTextAreaElement) => {
        if (textarea.scrollHeight > textarea.clientHeight) {
          textarea.style.height = textarea.scrollHeight + 'px';
        }
      };

      return {
        message,
        boxClass,
        textareaClass,
        captionClass,
        textarea,
        isFocused,
        focus,
        handleInput,
        handlePaste,
      };
    },
  });
